<template>
  <div id="app" :class="typeof weather.main != 'undefined' && weather.main.temp > 16 ? 'warm' : '' ">
    <main>
      <div class="search_box">
        <input
          type="text"
          class="search_bar"
          placeholder="Search ............"
          v-model="query"
          @keypress="fetchWeather"
        />
      </div>
      <div class="weather_wrap" v-if="typeof weather.main != 'undefined'">
        <div class="location_box">
          <div class="location">
            {{ weather.name }}, {{ weather.sys.country }}
          </div>
          <div class="date">{{ dateBuild() }}</div>
        </div>

        <div class="weather_box">
          <div class="temp">{{ Math.round(weather.main.temp) }} Ù’C</div>
          <p class="weather"> {{ weather.weather[0].main }} </p>
        </div>
      </div>
    </main>
  </div>
</template>
<script>
export default {
  name: "App",
  data() {
    return {
      api_key: "386f2de992e1dad6dbf033e9a820ca9d",
      url_base: "https://api.openweathermap.org/data/2.5/",
      query: "",
      weather: {},
    };
  },
  methods: {
    fetchWeather(e) {
      if (e.key === "Enter") {
        fetch(
          `${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`
        )
          .then((res) => {
            return res.json();
          })
          .then(this.setResults);
      }
    },
    setResults(results) {
      this.weather = results;
    },
    dateBuild() {
      const dateNow = new Date();
      const months = [
        'January',
        'Febuary',
        'March',
        'April',
        'May',
        'Jun',
        'July',
        'August',
        'Septemper',
        'October',
        'November',
        'December',
      ];
      let days = ["sunday", "Monday", "Tuesday", "Wednesday", "Thrusday", "Friday", "Saturday"];
      let day = days[dateNow.getDay()],
      date = dateNow.getDate(),
      month = months[dateNow.getMonth()],
      year = dateNow.getFullYear();
      return `${day} ${date} ${month} ${year}`;
    },
  },
};
</script>

